.MagentaA11y--home-page {
  --header-flex-direction: row;
  --title-first-headline-font: var(--magentaa11y-typeset-display-lg-strong);
  --title-second-headline-font: var(
    --magentaa11y-typeset-display-lg-xtrastrong
  );
  --header-max-height: 360px;
  --header-logo-width: fit-content;
  --logo-max-width: 410px;
  --logo-max-height: 350px;
  --logo-height: 410px;

  width: 100%;

  &__header {
    background: linear-gradient(
      to bottom,
      var(--magentaa11y-color-brand-primary-400) 0,
      var(--magentaa11y-color-brand-grayscale-black) 75%
    );

    @media (min-width: 768px) {
      background: linear-gradient(
        to right,
        var(--magentaa11y-color-brand-primary-400) 0,
        var(--magentaa11y-color-brand-grayscale-black) 75%
      );
    }

    &--wrapper {
      display: flex;
      flex-direction: column;
      padding: var(--magentaa11y-spacing-size-48)
        var(--magentaa11y-spacing-size-40);
      max-height: 250px;
      max-width: 900px;
      margin: 0 auto;
      width: 100%;
      height: var(--logo-max-height);
      justify-content: center;
      align-items: center;

      @media (min-width: 768px) {
        max-height: var(--header-max-height);
      }
    }

    &--title {
      flex-basis: 35%;
      display: flex;
      flex-direction: column;
      gap: var(--magentaa11y-spacing-size-16);
        color: var(--magentaa11y-color-brand-grayscale-white);
        word-break: keep-all;
        text-align: center;
        font: var(--magentaa11y-typeset-display-4xl-strong);

      @media (max-width: 768px) {
        font: var(--magentaa11y-typeset-display-lg-xtrastrong);
      }
    }

    &--subtitle {
    color: var(--magentaa11y-color-brand-grayscale-white);
    font: var(--magentaa11y-typeset-headline-sm-strong);
    text-align: center;
    margin-top: var(--magentaa11y-spacing-size-8);
    max-width: 70%;

    @media (max-width: 768px) {
    font: var(--magentaa11y-typeset-body-md-regular);
    max-width: 90%;}
  }
  }

  &__content {
    margin: 0 auto;
    padding: var(--magentaa11y-spacing-size-48)
      var(--magentaa11y-spacing-size-80);

    &--header {
      font: var(--magentaa11y-typeset-headline-xxl-xtrastrong);
    }

    p {
      font: var(--title-first-headline-font);

    }

    &--beige {
      background-color: var(--color-background-secondary);
    }

    @media (max-width: 768px) {
      padding: var(--magentaa11y-spacing-size-24)
        var(--magentaa11y-spacing-size-40);
    }
  }

  &__text--wrapper{
    margin:0 auto;
    max-width: 950px;
    line-height: var(--magentaa11y-line-height-24);
  }

  @media (max-width: 768px) {
    --header-flex-direction: column;
    --title-first-headline-font: var(--magentaa11y-typeset-headline-xs-strong); 
    --header-gradient: linear-gradient(
      to top,
      var(--magentaa11y-color-brand-primary-400) 0,
      var(--magentaa11y-color-brand-grayscale-black) 75%
    );
    --header-max-height: 355px;
    --header-logo-width: 100%;
    --logo-max-width: 40vw;
    --logo-height: auto;
  }

  .MagentaA11y {
    &__card {
      margin: 0 auto;
      max-width: 950px;
      padding: var(--magentaa11y-spacing-size-24) 0;

      @media (min-width: 1000px) {
        grid-template-columns: 1fr 1fr;
      }
    }
  }
}
